"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import api from "../../lib/axios";
import DashboardLayout from "../components/DashboardLayout";



export default function DashboardPage() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      router.push("/login");
      return;
    }

    const fetchUser = async () => {
      try {
        const res = await api.get("/auth/me", {
          headers: { Authorization: `Bearer ${token}` },
        });
        setUser(res.data);
      } catch {
        localStorage.removeItem("token");
        router.push("/login");
      }
    };

    fetchUser();
  }, [router]);

  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push("/login");
  };

  if (!user) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-indigo-500 via-purple-600 to-pink-500 text-white text-xl font-semibold">
        Loading...
      </div>
    );
  }

  return (
    <DashboardLayout user={user} onLogout={handleLogout}>
      {user.role === "admin" ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
          <div className="bg-white/10 p-6 rounded-xl shadow-lg">
            <h3 className="text-xl font-semibold mb-2">ðŸ‘¥ Manage Users</h3>
            <p className="text-sm text-white/80">View and control user roles.</p>
          </div>
          <div className="bg-white/10 p-6 rounded-xl shadow-lg">
            <h3 className="text-xl font-semibold mb-2">ðŸ“ˆ Reports</h3>
            <p className="text-sm text-white/80">Analyze usage and performance data.</p>
          </div>
        </div>
      ) : (
        <div className="bg-white/10 p-6 rounded-xl shadow-lg text-center">
          <h3 className="text-xl font-semibold mb-2">ðŸŽ‰ Welcome to Your Dashboard</h3>
          <p className="text-sm text-white/80">
            Explore personalized insights and your recent activity here.
          </p>
        </div>
      )}
    </DashboardLayout>
  );
}
